  padding: 0 2em;
}

/* underline to appear when scrolled */
.BorderBottom-Navigation {
  background: linear-gradient(90deg, #1a83a1, #675899) left bottom
    rgba(32, 31, 30, 0.8) no-repeat;
  background-size: 100% 0.8px;
  -webkit-backdrop-filter: blur(30px);
          backdrop-filter: blur(30px);
}

/* ==== Tech Hub IIT Tirupati BRAND NAME ==== */

.main-web-name-Navigation {
  font-size: 190%;
  background: linear-gradient(
    90deg,
    rgb(0, 185, 214),
    rgb(3, 167, 177),
    rgb(3, 163, 137)
  );
  background-size: 100%;
  background-clip: text;
  -webkit-background-clip: text;
  -moz-background-clip: text;
  -webkit-text-fill-color: transparent;
  -moz-text-fill-color: transparent;
}

/* ==== NAVIGATION LINKS ==== */

.navigation {
  box-sizing: border-box;
}
/* NavLink Styling */
a.nav-link-Navigation,
.nav-dropdown-Navigation {
  display: inline-block;
  position: relative;
  font-size: 120%;
  margin: 0 0.8em;
  white-space: nowrap;
}

a.nav-link-Navigation {
  padding: 0.5rem 1rem;
  justify-content: center;
  color: rgba(255, 255, 255, 0.5);
  display: flex;
  align-items: center;
  margin: 0 0.8em;
}
a.nav-link-Navigation:hover,
a.nav-link-Navigation.active {
  text-decoration: none;
  color: #f0f0f0;
}

.navbar-dark .navbar-nav .nav-link:hover {
  color: #f0f0f0;
}

a#collasible-nav-dropdown.dropdown-toggle.nav-link::after {
  content: none;
}

/* underline for the active page  */

a.nav-link-Navigation.nav-link.active::after,
a.nav-link-Navigation.active::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 5%;
  height: 2px;
  width: 90%;
  background: linear-gradient(
    90deg,
    rgba(0, 188, 242, 1),
    rgba(123, 163, 244, 1),
    rgba(180, 160, 255, 1)
  );
  border-radius: 15%;
}

/* underline transition for link clicked */
#collasible-nav-dropdown:link::before,
\ .nav-link-Navigation:link::before {
  content: '';
  position: absolute;
  width: 90%;
  transform: scaleX(0);
  -webkit-transform: scaleX(0);
  -moz-transform: scaleX(0);
  -ms-transform: scaleX(0);
  -o-transform: scaleX(0);
  height: 2px;
  bottom: 0;
  left: 5%;
  background: linear-gradient(
    90deg,
    rgba(0, 188, 242, 1),
    rgba(123, 163, 244, 1),
    rgba(180, 160, 255, 1)
  );
  transform-origin: bottom right;
  transition: transform ease-in-out 150ms;
  -webkit-transition: transform ease-in-out 150ms;
  -moz-transition: transform ease-in-out 150ms;
  -ms-transition: transform ease-in-out 150ms;
  -o-transition: transform ease-in-out 150ms;
  border-radius: 15%;
}

#collasible-nav-dropdown:focus::before,
.nav-link-Navigation:active::before {
  width: 90%;
  transform: scaleX(1);
  -webkit-transform: scaleX(1);
  -moz-transform: scaleX(1);
  -ms-transform: scaleX(1);
  -o-transform: scaleX(1);
  transform-origin: bottom left;
  border-radius: 15%;
}

/* drop-down menu items styling */
.nav-container-Navigation .dropdown-menu {
  z-index: unset;
  z-index: 2000;
}

#responsive-navbar-nav
  > div
  > div.nav-dropdown-Navigation.show.dropdown.nav-item
  > div {
  margin: 0;
  padding: 0;
  background-color: #30302f;
  opacity: 0.99;
}

.nav-dropdown-Navigation .about-menuitem-Navigation,
.nav-dropdown-Navigation .events-menuitem-Navigation,
.nav-dropdown-Navigation .projects-menuitem-Navigation,
.nav-dropdown-Navigation .techteams-menuitem-Navigation,
.nav-dropdown-Navigation .clubs-menuitem-Navigation {
