}

.nav-dropdown-Navigation .about-menuitem-Navigation:hover,
.nav-dropdown-Navigation .events-menuitem-Navigation:hover,
.nav-dropdown-Navigation .projects-menuitem-Navigation:hover,
.nav-dropdown-Navigation .techteams-menuitem-Navigation:hover,
.nav-dropdown-Navigation .clubs-menuitem-Navigation:hover {
