/* ==== MAIN CONTAINER ==== */

.nav-container-EventsNavbar {
  background-color: #20201f;
  font-family: 'Signika', Arial, Helvetica, sans-serif;
  position: -webkit-sticky;
  position: sticky;
  top: -7px;
  z-index: 0;
  margin-bottom: 3%;
}
.nav-container-EventsNavbar .navbar {
  padding-top: 1%;
}

/* underline to appear when scrolled */
.BorderBottom-EventsNavbar {
  background: linear-gradient(90deg, #1a83a1, #675899) left bottom
    rgba(32, 31, 30, 0.8) no-repeat;
  background-size: 100% 0.8px;
  -webkit-backdrop-filter: blur(30px);
          backdrop-filter: blur(30px);
  z-index: 100;
}

/* ==== Events BRAND NAME ==== */

.main-web-name-EventsNavbar {
  font-family: 'Roboto Slab', Arial, Helvetica, sans-serif;
  font-size: 150%;
  background: linear-gradient(
    90deg,
    rgb(0, 185, 214),
    rgb(3, 167, 177),
    rgb(3, 163, 137)
  );
  background-size: 100%;
  background-clip: text;
  -webkit-background-clip: text;
  -moz-background-clip: text;
  -webkit-text-fill-color: transparent;
  -moz-text-fill-color: transparent;
}

/* ==== EVENTSNAVBAR NAVLINKS ==== */

/* WITHOUT SCROLL */
.nav-link-container-EventsNavbar {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  font-size: 1rem;
  font-weight: 400;
  white-space: nowrap;
  text-transform: uppercase;
  font-style: normal;
  line-height: 1.5rem;
  letter-spacing: -0.015em;
  background: #201f1e;
  margin: 0.3em auto;
  color: #f0f0f0;
  box-shadow: -2px -2px 2px rgba(191, 189, 200, 0.08),
    2px 2px 3px rgba(0, 0, 0, 0.4), -10px -10px 15px rgba(208, 197, 249, 0.005),
    10px 10px 10px rgba(0, 0, 0, 0.1);
  border-radius: 5px;
  width: 15%;
  height: 40px;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -ms-border-radius: 5px;
  -o-border-radius: 5px;
  justify-content: center;
  padding: 2px 2.5px;
}

.nav-link-EventsNavbar {
  height: 100%;
  border-radius: 7px;
  width: 100%;
  -webkit-border-radius: 7px;
  -moz-border-radius: 7px;
  -ms-border-radius: 7px;
  -o-border-radius: 7px;
}

.nav-link-container-EventsNavbar-scrolled a {
  background: #20201f;
}

.nav-container-EventsNavbar a.active {
  background: linear-gradient(90deg, #13657d, #615983);
}

.nav-container-EventsNavbar a:hover {
  text-decoration: none;
}

#responsive-navbar-nav > div > div > a.nav-link-EventsNavbar.nav-link.active {
  background: linear-gradient(90deg, #13657d, #615983);
}

/* WITH SCROLL */
.nav-link-container-EventsNavbar-scrolled {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  font-size: 1rem;
  font-weight: 400;
  white-space: nowrap;
  text-transform: uppercase;
  font-style: normal;
  line-height: 1.5rem;
  letter-spacing: -0.015em;
  background: linear-gradient(180deg, #00bcf2, #b4a0ff);
  margin: 0.3em auto;
  color: #f0f0f0;
  /* box-shadow: -2px -2px 2px rgba(191, 189, 200, 0.08), 2px 2px 3px rgba(0, 0, 0, 0.4), -10px -10px 15px rgba(208, 197, 249, 0.005), 10px 10px 10px rgba(0, 0, 0, 0.1); */
  border-radius: 5px;
  width: 15%;
  height: 40px;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -ms-border-radius: 5px;
  -o-border-radius: 5px;
  justify-content: center;
  padding: 1px;
}

.nav-link-EventsNavbar-scrolled {
  height: 100%;
  border-radius: 5px;
  width: 100%;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -ms-border-radius: 5px;
  -o-border-radius: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
}

#responsive-navbar-nav > div > div > a.nav-link-EventsNavbar-scrolled.nav-link {
  background: #201f1e;
}

#responsive-navbar-nav
  > div
  > div
  > a.nav-link-EventsNavbar-scrolled.nav-link.active {
  background: linear-gradient(90deg, #184653, #443f56);
  color: #f0f0f0;
}

/* gradient for all events and calender events */
#responsive-navbar-nav > div > a:nth-child(1) > div,
#responsive-navbar-nav
  > div
  > a.nav-link-EventsNavbar.calender-events-EventsNavbar.nav-link
  > div {
  font-weight: 400;
  background: linear-gradient(#3d91c7, #5a6795);
  -webkit-background-clip: text;
          background-clip: text;
  -webkit-text-fill-color: transparent;
}

/* WORD COLORING FOR NAV LINKS */

#events-navbar-Workshops,
#events-navbar-Talks,
#events-navbar-Activities,
#events-navbar-Competitions {
  color: #d1d1d0;
}

#events-navbar-AllEvents > div,
#events-navbar-CalendarEvents > div {
  font-weight: 400;
  background: linear-gradient(180deg, #00bcf2, #7593da, #b4a0ff);
  background-size: 100%;
  background-clip: text;
  -webkit-background-clip: text;
  -moz-background-clip: text;
  -webkit-text-fill-color: transparent;
  -moz-text-fill-color: transparent;
}

#responsive-navbar-nav > div > div > a.nav-link-EventsNavbar.nav-link.active {
  color: #ffffff;
}

