.event-card-container {
  float: left;
  padding: 10px;
}

.event-card-content {
  position: relative;
  width: 100%;
  max-width: 578px;
  margin: auto;
  overflow: hidden;
  background: #201f1e;
  box-shadow: -2px -2px 2px rgba(191, 189, 200, 0.08),
    2px 2px 3px rgba(0, 0, 0, 0.4), -10px -10px 15px rgba(208, 197, 249, 0.005),
    10px 10px 10px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  padding: 10px;
}

.event-card-content .event-card-content-overlay {
  background: rgba(32, 31, 30, 0.9);
  position: absolute;
  height: 100%;
  width: 100%;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  opacity: 0;
  transition: all 0.4s ease-in-out 0s;
}

.event-card-content:hover .event-card-content-overlay {
  opacity: 1;
}

.event-card-content-image {
  width: 100%;
  border-radius: 10px;
}

